import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { useNavigate } from 'react-router-dom';
import { useState, useEffect } from 'react';

import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';

import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import AppBar from '@mui/material/AppBar';
import StorageService from '../../../common/storage';
import Snackbar from '@mui/material/Snackbar';


const defaultTheme = createTheme();

export default function SignIn() {
    const [formData, setFormData] = useState({
        phone: "",
        password: "",
        phoneCode: "",
    });

    const navigate = useNavigate();
    const [phoneCodes, setPhoneCodes] = useState([]);

    const handleInputChange = (event) => {
        const { name, value } = event.target;
        setFormData({
            ...formData,
            [name]: value,
        });
    };

    useEffect(() => {
        const fetchDropDowns = async () => {
            try {
                const response = await fetch(StorageService.BASIC_URL + `/api/auth/enums/all`);
                const data = await response.json();
                console.log(data);
                setPhoneCodes(data.phoneCode);
            } catch (error) {
                console.error("Error fetching users:", error.message);
            }
        };

        fetchDropDowns();
    }, [])

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const userData = {
                password: formData.password,
                phone: formData.phoneCode + formData.phone
            };
            // userData.phone = formData.phoneCode + formData.phone
            const response = await fetch(StorageService.BASIC_URL + `/api/auth/login`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify(userData),
            });

            if (!response.ok) {
                console.log("in not ok")
                setOpen(true);
            } else if (response.ok) {
                const data = await response.json();
                console.log(data);
                if (data.jwt) {
                    StorageService.removeToken();
                    localStorage.setItem("kaaj-token", data.jwt);
                    const user = {
                        id: data.userId,
                        role: data.userRole
                    }
                    localStorage.setItem("kaaj-user", JSON.stringify(user));
                    if (data.infoAdded) {
                        navigate("/dashboard");
                    } else {
                        navigate("/create-candidate");
                    }

                }
            }
        } catch (error) {
            console.error("Error authenticating user:", error.message);
        }
    };

    const handleSignUpClick = () => {
        navigate("/name");
    }

    //Snakebar

    const [open, setOpen] = React.useState(false);

    const handleClick = () => {
        setOpen(true);
    };

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }

        setOpen(false);
    };

    return (
        <ThemeProvider theme={defaultTheme}>
            <AppBar position="static">
                <Toolbar variant="dense">
                    <IconButton edge="start" color="inherit" aria-label="menu" sx={{ mr: 2 }}>
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" color="inherit" component="div">
                        Kaaj
                    </Typography>
                </Toolbar>
            </AppBar>

            <Snackbar open={open} autoHideDuration={5000} anchorOrigin={{ vertical: 'top', horizontal: 'center' }} onClose={handleClose} message="Wrong phone number or password.">

            </Snackbar>

            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <Box
                    sx={{
                        marginTop: 8,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >
                    <Avatar sx={{ m: 1, bgcolor: 'primary.main' }}>
                        <LockOutlinedIcon />
                    </Avatar>
                    <Typography component="h1" variant="h5">
                        Sign in
                    </Typography>
                    <Box component="form" onSubmit={handleSubmit} noValidate >
                        <Grid item xs={12} style={{ display: 'flex', alignItems: 'center' }}>
                            <FormControl variant="standard" style={{ width: '30%', marginRight: '2%', marginTop: '8px' }}>
                                <InputLabel id="phoneCode">Code</InputLabel>
                                <Select
                                    fullWidth
                                    required
                                    labelId="phoneCode"
                                    name="phoneCode"
                                    id="phoneCode"
                                    value={formData.phoneCode}
                                    onChange={handleInputChange}
                                >
                                    {Object.entries(phoneCodes).map(([key, value]) => (
                                        <MenuItem key={key} value={value}>
                                            {value}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </FormControl>
                            <TextField
                                margin="normal"
                                required
                                fullWidth
                                id="phone"
                                label="Phone Number"
                                name="phone"
                                autoComplete="phone"
                                autoFocus
                                value={formData.phone}
                                variant="standard"
                                onChange={handleInputChange}
                            />
                        </Grid>
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            name="password"
                            label="Password"
                            type="password"
                            id="password"
                            autoComplete="current-password"
                            value={formData.password}
                            variant="standard"
                            onChange={handleInputChange}
                        />
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            sx={{ mt: 3, mb: 2 }}
                            disabled={!formData.phone || !formData.password || !formData.phoneCode}
                        >
                            Sign In
                        </Button>

                        <Button
                            fullWidth
                            variant="outlined"
                            onClick={handleSignUpClick}
                        >
                            Don't have an account?
                        </Button>

                        {/* <Grid container>
                            <Grid item>
                                <Link variant="body2" onClick={handleSignUpClick}>
                                    {"Don't have an account? Sign Up"}
                                </Link>
                            </Grid>
                        </Grid> */}
                    </Box>
                </Box>
            </Container>
        </ThemeProvider>
    );
}