import React, { useState, useEffect } from "react";
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { useNavigate } from 'react-router-dom';
import { useParams } from 'react-router-dom';
import MenuItem from '@mui/material/MenuItem';
import InputLabel from '@mui/material/InputLabel';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import OutlinedInput from '@mui/material/OutlinedInput';

import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
// import { AdapterDayjs } from '@mui/x-date-pickers/adapter-dayjs';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import dayjs from 'dayjs';

import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import AppBar from '@mui/material/AppBar';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import { Country, City } from 'country-state-city';

import Chip from '@mui/material/Chip';
import StorageService from "../../common/storage";

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
            width: 250,
        },
    },
};


const defaultTheme = createTheme();

export default function AddCountryPrefreneces() {

    const [formData, setFormData] = useState({
        country: "",
        cities: [],
    });
    let { id } = useParams();
    // const [dropDowns, setDropDowns] = useState();
    // const [genderOptions, setGenderOptions] = useState([]);

    const [countries, setCountry] = useState(Country.getAllCountries());

    const [cities, setCities] = useState([]);

    const navigate = useNavigate();

    const dobDate = dayjs(formData.dob);

    useEffect(() => {

        const fetchDropDowns = async () => {
            try {
                const response = await fetch(StorageService.BASIC_URL + `/api/auth/enums/all`);
                const data = await response.json();
                console.log(data);
                console.log(countries)
                // setDropDowns(data);
                // const genderData = response.data.gender;
                // setGenderOptions(data.gender);
                // setMaritalStatus(data.maritalStatus);
                // setCurrency(data.currency);
                // console.log("genderOptions",genderOptions)
            } catch (error) {
                console.error("Error fetching users:", error.message);
            }
        };

        fetchDropDowns();
    }, [])

    const handleInputChange = (event, countryUpdated) => {

        const { name, value } = event.target;
        if (countryUpdated) {
            setOpenBackDrop(true);
            console.log(value);
            const country = countries.find(country => country.name === value);
            console.log(country);
            setCities(City.getCitiesOfCountry(country.isoCode));
            setOpenBackDrop(false);
        }
        setFormData({
            ...formData,
            [name]: value,
        });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            if (StorageService.tokenExpired()) {
                navigate("/");
            }
            setOpenBackDrop(true);
            const userData = formData;
            const response = await fetch(StorageService.BASIC_URL + "/api/candidate/country-preferences", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    "Authorization": `Bearer ${localStorage.getItem("kaaj-token")}`
                },
                body: JSON.stringify(userData),
            });
            if (!response.ok) {
                setOpenBackDrop(false);
                throw new Error("Signup failed");
            } else if (response.ok) {
                setOpenBackDrop(false);
                navigate("/view-candidate-country-preferences");
            }
        } catch (error) {
            console.error("Error signup user:", error.message);
        }
    };

    const [openBackDrop, setOpenBackDrop] = React.useState(false);

    const handleCloseBackDrop = () => {
        setOpenBackDrop(false);
    };
    const handleOpenBackDrop = () => {
        setOpenBackDrop(true);
    };


    return (
        <ThemeProvider theme={defaultTheme}>

            <Backdrop
                sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                open={openBackDrop}
                onClick={handleCloseBackDrop}
            >
                <CircularProgress color="inherit" />
            </Backdrop>
            <AppBar position="static">
                <Toolbar variant="dense">
                    <IconButton edge="start" color="inherit" aria-label="menu" sx={{ mr: 2 }}>
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" color="inherit" component="div">
                        Add Country Preference
                    </Typography>
                </Toolbar>
            </AppBar>

            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <Box
                    sx={{
                        marginTop: 8,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >

                    <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}>
                        <Grid container spacing={2}>

                            <Grid item xs={12}>
                                <InputLabel id="country">Country</InputLabel>
                                <Select
                                    fullWidth
                                    required
                                    name="country"
                                    label="Country"
                                    id="country"
                                    value={formData.country}
                                    // onChange={handleInputChange}
                                    onChange={(event) => handleInputChange(event, true)}
                                    variant="standard"
                                >

                                    {countries.map(country => (
                                        <MenuItem key={country.isoCode} value={country.name}>
                                            {country.name}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </Grid>
                            <Grid item xs={12}>
                                <InputLabel id="country">Cities</InputLabel>
                                <Select
                                    fullWidth
                                    variant="standard"

                                    required
                                    name="cities"
                                    label="Cities"
                                    id="cities"
                                    multiple
                                    value={formData.cities}
                                    // onChange={handleInputChange}
                                    onChange={(event) => handleInputChange(event, false)}
                                    sx={{ minWidth: 400, maxWidth: 400 }}
                                    // input={<OutlinedInput id="select-multiple-chip" label="Chip" />}
                                    renderValue={(selected) => (
                                        <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                                            {selected.map((value) => (
                                                <Chip key={value} label={value} />
                                            ))}
                                        </Box>
                                    )}
                                    MenuProps={MenuProps}
                                >

                                    {cities.map(city => (
                                        <MenuItem key={city.isoCode} value={city.name}>
                                            {city.name}
                                        </MenuItem>
                                    ))}
                                </Select>
                                {/* <TextField
                                    required
                                    fullWidth
                                    type="number"
                                    name="maxSalaryExpectation"
                                    label="Max Salary Expectation"
                                    id="maxSalaryExpectation"
                                    autoComplete="maxSalaryExpectation"
                                    value={formData.maxSalaryExpectation}
                                    variant="standard"
                                    onChange={(event) => handleInputChange(event, true)}
                                /> */}
                            </Grid>
                            {/* <Grid item xs={12}>
                                <InputLabel id="maxSalaryExpCurrency">Max Salary Exp Currency</InputLabel>
                                <Select
                                    fullWidth
                                    required
                                    name="maxSalaryExpCurrency"
                                    label="Max Salary Exp Currency"
                                    id="maxSalaryExpCurrency"
                                    value={formData.maxSalaryExpCurrency}
                                    onChange={handleInputChange}
                                    variant="standard"
                                >

                                    {Object.entries(currency).map(([key, value]) => (
                                        <MenuItem key={key} value={value}>
                                            {value}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </Grid> */}

                        </Grid>
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            sx={{ mt: 3, mb: 2 }}
                            disabled={!formData.country || !formData.cities.length}
                        >
                            Continue
                        </Button>

                    </Box>
                </Box>
            </Container>
        </ThemeProvider>
    );
}