import React, { useState, useEffect } from "react";
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { useNavigate } from 'react-router-dom';
import { useParams } from 'react-router-dom';
import MenuItem from '@mui/material/MenuItem';
import InputLabel from '@mui/material/InputLabel';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import { styled } from '@mui/material/styles';

import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
// import { AdapterDayjs } from '@mui/x-date-pickers/adapter-dayjs';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import dayjs from 'dayjs';
import Snackbar from '@mui/material/Snackbar';
import Alert from '@mui/material/Alert';
import { getStorage, uploadBytes, ref, getDownloadURL } from "firebase/storage";
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import { initializeApp } from "firebase/app";

import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import AppBar from '@mui/material/AppBar';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import StorageService from "../../common/storage";

const VisuallyHiddenInput = styled('input')({
    clip: 'rect(0 0 0 0)',
    clipPath: 'inset(50%)',
    height: 1,
    overflow: 'hidden',
    position: 'absolute',
    bottom: 0,
    left: 0,
    whiteSpace: 'nowrap',
    width: 1,
});

// Initialize Firebase
const firebaseConfig = {
    apiKey: "AIzaSyCfvLfrBB2SHl3UMLVgZBhucrxkZH-s1yE",
    authDomain: "kaaj-2fa4b.firebaseapp.com",
    projectId: "kaaj-2fa4b",
    storageBucket: "kaaj-2fa4b.appspot.com",
    messagingSenderId: "545235033893",
    appId: "1:545235033893:web:df4fc4da3660d8251884cc"
};

const app = initializeApp(firebaseConfig);
const storage = getStorage(app);




const defaultTheme = createTheme();

export default function CreateDrivingLicence() {

    const [formData, setFormData] = useState({
        licenseCategory: "",
    });
    let { id } = useParams();
    // const [dropDowns, setDropDowns] = useState();
    const [candidateFileDTO, setCandidateFileDTO] = useState(null);
    const [licenceOptions, setLicenceOptions] = useState([]);

    const navigate = useNavigate();

    const dobDate = dayjs(formData.dob);

    const extractTokenFromUrl = (fullUrl) => {
        const url = new URL(fullUrl);
        const token = url.searchParams.get("token");
        console.log(token);
        return token;
    };

    useEffect(() => {

        const fetchDropDowns = async () => {
            try {
                const response = await fetch(StorageService.BASIC_URL + `/api/auth/enums/all`);
                const data = await response.json();
                console.log(data);
                // setDropDowns(data);
                // const genderData = response.data.gender;
                // setGenderOptions(data.gender);
                // setMaritalStatus(data.maritalStatus);
                setLicenceOptions(data.licenseCategory);
                // console.log("genderOptions",genderOptions)
            } catch (error) {
                console.error("Error fetching users:", error.message);
            }
        };

        fetchDropDowns();
    }, [])

    const handleInputChange = (event) => {
        const { name, value } = event.target;
        setFormData({
            ...formData,
            [name]: value,
        });
    };

    const handleFileChange = (e) => {
        uploadFile(e.target.files[0])
    };

    const generateFileName = () => {
        // Get current date and time
        const currentDate = new Date();
        const year = currentDate.getFullYear();
        const month = String(currentDate.getMonth() + 1).padStart(2, '0'); // Months are zero-indexed
        const day = String(currentDate.getDate()).padStart(2, '0');
        const hours = String(currentDate.getHours()).padStart(2, '0');
        const minutes = String(currentDate.getMinutes()).padStart(2, '0');
        const seconds = String(currentDate.getSeconds()).padStart(2, '0');

        // Generate file name
        const fileName = `file_${year}${month}${day}_${hours}${minutes}${seconds}`;
        console.log("filename", fileName);
        return fileName;
    }

    const uploadFile = async (file) => {
        if (!file) return;
        setOpenBackDrop(true);
        const fileName = file.name;
        const fileExtension = fileName.slice(((fileName.lastIndexOf(".") - 1) >>> 0) + 2);
        // const generatedName = generateFileName()
        // console.log(`profiles/${generateFileName()}.` + fileExtension)
        const storageRef = ref(storage, `licenses/${generateFileName()}.${fileExtension}`);
        const res = await uploadBytes(storageRef, file);
        console.log("upload done", res)
        console.log("downloadTokens", res.metadata.downloadTokens);
        const downloadURL = await getDownloadURL(storageRef);
        const token = extractTokenFromUrl(downloadURL);
        console.log(downloadURL);

        const candidateFile = {
            fileName: res.metadata.fullPath,
            token: token,
            fullPath: downloadURL,
        }

        setCandidateFileDTO(candidateFile);
        setOpenBackDrop(false);

    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            if (StorageService.tokenExpired()) {
                navigate("/");
            }
            setOpenBackDrop(true);
            const userData = formData;
            userData.candidateFileDTO = candidateFileDTO;
            const response = await fetch(StorageService.BASIC_URL + "/api/candidate/driving-licence", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    "Authorization": `Bearer ${localStorage.getItem("kaaj-token")}`
                },
                body: JSON.stringify(userData),
            });
            if (!response.ok) {
                setOpenBackDrop(false);
                throw new Error("Signup failed");
            } else if (response.ok) {
                setOpenBackDrop(false);
                navigate("/view-candidate-driving-licences");
            }
        } catch (error) {
            console.error("Error signup user:", error.message);
        }
    };


    //Snakebar

    const [open, setOpen] = React.useState(false);

    const handleClick = () => {
        setOpen(true);
    };

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }

        setOpen(false);
    };

    const [openBackDrop, setOpenBackDrop] = React.useState(false);

    const handleCloseBackDrop = () => {
        setOpen(false);
    };
    const handleOpenBackDrop = () => {
        setOpen(true);
    };

    return (
        <ThemeProvider theme={defaultTheme}>
            <Backdrop
                sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                open={openBackDrop}
                onClick={handleCloseBackDrop}
            >
                <CircularProgress color="inherit" />
            </Backdrop>
            <AppBar position="static">
                <Toolbar variant="dense">
                    <IconButton edge="start" color="inherit" aria-label="menu" sx={{ mr: 2 }}>
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" color="inherit" component="div">
                        Driving Licence
                    </Typography>
                </Toolbar>
            </AppBar>
            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <Box
                    sx={{
                        marginTop: 8,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >
                    <Snackbar open={open} autoHideDuration={6000} anchorOrigin={{ vertical: 'top', horizontal: 'center' }} onClose={handleClose}>
                        <Alert
                            onClose={handleClose}
                            severity="success"
                            variant="filled"
                            sx={{ width: '100%' }}
                        >
                            Docoment Uploaded Successfully.
                        </Alert>
                    </Snackbar>

                    <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}>
                        <Grid container spacing={2}>

                            <Grid item xs={12}>
                                <InputLabel id="licenseCategory">License Category</InputLabel>
                                <Select
                                    fullWidth
                                    required
                                    name="licenseCategory"
                                    label="License Category"
                                    id="licenseCategory"
                                    value={formData.licenseCategory}
                                    onChange={handleInputChange}
                                    variant="standard"

                                >

                                    {Object.entries(licenceOptions).map(([key, value]) => (
                                        <MenuItem key={key} value={key}>
                                            {value}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </Grid>

                            <Grid item xs={12}>
                                <Typography sx={{ pt: 2, pb: 2 }}>Upload license</Typography>
                                {candidateFileDTO == null && (<Button
                                    component="label"
                                    role={undefined}
                                    variant="contained"
                                    tabIndex={-1}
                                    fullWidth
                                    startIcon={<CloudUploadIcon />}
                                >
                                    Upload file
                                    <VisuallyHiddenInput type="file" onChange={handleFileChange} />
                                </Button>
                                )}
                            </Grid>

                            <Grid item xs={12}>
                                {candidateFileDTO != null && (<Button
                                    component="label"
                                    role={undefined}
                                    variant="contained"
                                    tabIndex={-1}
                                    startIcon={<CheckCircleIcon />}
                                    fullWidth
                                    color="success"
                                >
                                    License Uploaded
                                </Button>
                                )}
                            </Grid>



                            <Grid item xs={12}>
                                <Button
                                    type="submit"
                                    fullWidth
                                    variant="contained"
                                    sx={{ mt: 3, mb: 2 }}
                                    disabled={candidateFileDTO == null || !formData.licenseCategory}
                                >
                                    Continue
                                </Button>
                            </Grid>
                        </Grid>

                    </Box>
                </Box>
            </Container>
        </ThemeProvider>
    );
}