import React, { useState, useEffect } from "react";
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { useNavigate } from 'react-router-dom';
import { useParams } from 'react-router-dom';
import MenuItem from '@mui/material/MenuItem';
import InputLabel from '@mui/material/InputLabel';
import Select, { SelectChangeEvent } from '@mui/material/Select';

import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
// import { AdapterDayjs } from '@mui/x-date-pickers/adapter-dayjs';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import dayjs from 'dayjs';

import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import AppBar from '@mui/material/AppBar';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import StorageService from "../../common/storage";


const defaultTheme = createTheme();

export default function CreateExperience() {

    const [formData, setFormData] = useState({
        jobTitle: "",
        workExperience: "",
        companyName: "",
        startDate: "",
        endDate: "",
        country: "",
        salary: "",
        salaryCurrency: "",
    });
    let { id } = useParams();
    // const [dropDowns, setDropDowns] = useState();
    const [jobTitleOptions, setjobTitleOptions] = useState([]);
    const [currencyOptions, setCurrencyOptions] = useState([]);
    const [country, setCountry] = useState([]);

    const navigate = useNavigate();

    const startDateDayJs = dayjs(Date.now());
    const endDateDayJs = dayjs(Date.now());

    useEffect(() => {

        const fetchDropDowns = async () => {
            try {
                const response = await fetch(StorageService.BASIC_URL + `/api/auth/enums/all`);
                const data = await response.json();
                console.log(data);
                // setDropDowns(data);
                // const genderData = response.data.gender;
                setjobTitleOptions(data.jobTitle);
                setCurrencyOptions(data.currency);
                setCountry(data.country);

                // setFormData({ ...formData, startDate: Date.now() });
                setFormData({ ...formData, endDate: Date.now(), startDate: Date.now()  });
                // console.log("genderOptions",genderOptions)
            } catch (error) {
                console.error("Error fetching users:", error.message);
            }
        };

        fetchDropDowns();
    }, [])

    const handleInputChange = (event) => {
        const { name, value } = event.target;
        setFormData({
            ...formData,
            [name]: value,
        });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            if (StorageService.tokenExpired()) {
                navigate("/");
            }
            setOpenBackDrop(true);
            const userData = formData;
            const response = await fetch(StorageService.BASIC_URL + "/api/candidate/experience", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    "Authorization": `Bearer ${localStorage.getItem("kaaj-token")}`
                },
                body: JSON.stringify(userData),
            });
            if (!response.ok) {
                setOpenBackDrop(false);
                throw new Error("Signup failed");
            } else if (response.ok) {
                setOpenBackDrop(false);
                navigate("/view-candidate-experience");
            }

        } catch (error) {
            console.error("Error signup user:", error.message);
        }
    };

    const [openBackDrop, setOpenBackDrop] = React.useState(false);

    const handleCloseBackDrop = () => {
        setOpenBackDrop(false);
    };
    const handleOpenBackDrop = () => {
        setOpenBackDrop(true);
    };

    return (
        <ThemeProvider theme={defaultTheme}>

            <Backdrop
                sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                open={openBackDrop}
                onClick={handleCloseBackDrop}
            >
                <CircularProgress color="inherit" />
            </Backdrop>
            <AppBar position="static">
                <Toolbar variant="dense">
                    <IconButton edge="start" color="inherit" aria-label="menu" sx={{ mr: 2 }}>
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" color="inherit" component="div">
                        Add work experience
                    </Typography>
                </Toolbar>
            </AppBar>

            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <Box
                    sx={{
                        marginTop: 8,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >

                    <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}>
                        <Grid container spacing={2}>
                            <Grid item xs={12}>
                                <InputLabel id="jobTitle">job Title</InputLabel>
                                <Select
                                    fullWidth
                                    required
                                    name="jobTitle"
                                    label="job Title *"
                                    id="jobTitle"
                                    value={formData.jobTitle}
                                    onChange={handleInputChange}
                                    variant="standard"
                                >

                                    {Object.entries(jobTitleOptions).map(([key, value]) => (
                                        <MenuItem key={key} value={key}>
                                            {value}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </Grid>

                            <Grid item xs={12}>
                                <TextField
                                    required
                                    fullWidth
                                    type="number"
                                    name="workExperience"
                                    label="Work Experience (Years)"
                                    id="workExperience"
                                    autoComplete="workExperience"
                                    value={formData.workExperience}
                                    variant="standard"
                                    onChange={handleInputChange}
                                />
                            </Grid>

                            <Grid item xs={12}>
                                <TextField
                                    required
                                    fullWidth
                                    name="companyName"
                                    label="Company Name"
                                    id="companyName"
                                    autoComplete="companyName"
                                    value={formData.companyName}
                                    variant="standard"
                                    onChange={handleInputChange}
                                />
                            </Grid>

                            <Grid item xs={12}>
                                <LocalizationProvider dateAdapter={AdapterDayjs}>
                                    <DatePicker
                                        label="From"
                                        value={startDateDayJs}
                                        required
                                        fullWidth
                                        name="startDate"
                                        id="startDate"
                                        variant="standard"
                                        onChange={(date) => setFormData({ ...formData, startDate: date })}
                                        renderInput={(params) => <TextField {...params} />}
                                        slotProps={{ textField: { variant: "standard", fullWidth: true  } }}
                                    />
                                </LocalizationProvider>
                            </Grid>

                            <Grid item xs={12}>
                                <LocalizationProvider dateAdapter={AdapterDayjs}>
                                    <DatePicker
                                        label="To"
                                        value={endDateDayJs}
                                        required
                                        fullWidth
                                        name="endDate"
                                        id="endDate"
                                        variant="standard"
                                        onChange={(date) => setFormData({ ...formData, endDate: date })}
                                        renderInput={(params) => <TextField {...params} />}
                                        slotProps={{ textField: { variant: "standard", fullWidth: true  } }}
                                    />
                                </LocalizationProvider>
                            </Grid>


                            <Grid item xs={12}>
                                <InputLabel id="country">Country</InputLabel>
                                <Select
                                    fullWidth
                                    required
                                    name="country"
                                    label="Country *"
                                    id="country"
                                    value={formData.country}
                                    onChange={handleInputChange}
                                    variant="standard"
                                >
                                    {Object.entries(country).map(([key, value]) => (
                                        <MenuItem key={key} value={key}>
                                            {value}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </Grid>

                            <Grid item xs={12}>
                                <TextField
                                    required
                                    fullWidth
                                    type="number"
                                    name="salary"
                                    label="Salary"
                                    id="salary"
                                    autoComplete="salary"
                                    value={formData.salary}
                                    variant="standard"
                                    onChange={handleInputChange}
                                />
                            </Grid>

                            <Grid item xs={12}>
                                <InputLabel id="salaryCurrency">Salary Currency</InputLabel>
                                <Select
                                    fullWidth
                                    required
                                    name="salaryCurrency"
                                    label="Salary Currency *"
                                    id="salaryCurrency"
                                    value={formData.salaryCurrency}
                                    onChange={handleInputChange}
                                    variant="standard"
                                >
                                    {Object.entries(currencyOptions).map(([key, value]) => (
                                        <MenuItem key={key} value={key}>
                                            {value}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </Grid>
                        </Grid>
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            sx={{ mt: 3, mb: 2 }}
                            disabled={!formData.companyName || !formData.country || !formData.endDate || 
                                !formData.jobTitle || !formData.salary || !formData.salaryCurrency || !formData.startDate || !formData.workExperience}
                        >
                            Continue
                        </Button>

                    </Box>
                </Box>
            </Container>
        </ThemeProvider>
    );
}