import React, { useState, useEffect } from "react";
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { useNavigate } from 'react-router-dom';
import { useParams } from 'react-router-dom';
import MenuItem from '@mui/material/MenuItem';
import InputLabel from '@mui/material/InputLabel';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import './css/ViewLanguages.css'

import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
// import { AdapterDayjs } from '@mui/x-date-pickers/adapter-dayjs';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { makeStyles } from '@mui/material/styles';
import Card from '@mui/material/Card';
import { styled } from '@mui/material/styles';
import CardContent from '@mui/material/CardContent';
import dayjs from 'dayjs';

import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import AppBar from '@mui/material/AppBar';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import StorageService from "../../common/storage";

const StyledCard = styled(Card)({
    minWidth: 275,
});

const StyledPos = styled(Typography)`
    margin-bottom: 12px;
    color: textSecondary;
  `;



const defaultTheme = createTheme();

export default function ViewEducations() {

    const [formData, setFormData] = useState({
        language: "",
        proficiency: "",
    });
    let { id } = useParams();
    // const [dropDowns, setDropDowns] = useState();
    // const [genderOptions, setGenderOptions] = useState([]);
    const [educations, setEducations] = useState([]);


    const navigate = useNavigate();

    const dobDate = dayjs(formData.dob);

    useEffect(() => {

        const fetchDropDowns = async () => {
            try {
                if (StorageService.tokenExpired()) {
                    navigate("/");
                }
                setOpenBackDrop(true);
                const response = await fetch(StorageService.BASIC_URL + `/api/candidate/education`, {
                    method: "GET",
                    headers: {
                        "Content-Type": "application/json",
                        "Authorization": `Bearer ${localStorage.getItem("kaaj-token")}`
                    },
                });
                const data = await response.json();
                console.log(data);
                setEducations(data);
                setOpenBackDrop(false);
            } catch (error) {
                console.error("Error fetching users:", error.message);
            }
        };

        fetchDropDowns();
    }, [])

    const handleInputChange = (event) => {
        const { name, value } = event.target;
        setFormData({
            ...formData,
            [name]: value,
        });
    };


    const [openBackDrop, setOpenBackDrop] = React.useState(false);

    const handleCloseBackDrop = () => {
        setOpenBackDrop(false);
    };
    const handleOpenBackDrop = () => {
        setOpenBackDrop(true);
    };

    return (
        <ThemeProvider theme={defaultTheme}>

            <Backdrop
                sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                open={openBackDrop}
                onClick={handleCloseBackDrop}
            >
                <CircularProgress color="inherit" />
            </Backdrop>
            <AppBar position="static">
                <Toolbar variant="dense">
                    <IconButton edge="start" color="inherit" aria-label="menu" sx={{ mr: 2 }}>
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" color="inherit" component="div">
                        View Education Details
                    </Typography>
                </Toolbar>
            </AppBar>

            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <Box
                    sx={{
                        marginTop: 8,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >

                    {educations.length === 0 && (
                        <div className="card">
                            <Typography variant="h5" color="primary" className="card-title">
                                No Education are present
                            </Typography>
                        </div>
                    )}

                    {educations.map(education => (
                        <div className="card">
                            <Typography variant="h5" color="primary" className="card-title">
                                {education.educationLevel}
                            </Typography>
                            <div className="card-content">
                                {education.university}
                            </div>
                        </div>
                    ))}

                    {/* <Link
                        component="button"
                        variant="body2"
                        onClick={() => {
                            navigate('/add-candidate-education')
                        }}
                    >
                        Add New Education
                    </Link> */}

                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        sx={{ mt: 3, mb: 2 }}
                        onClick={() => {
                            navigate('/add-candidate-education')
                        }}
                    >
                        Add New Education
                    </Button>

                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        sx={{ mt: 3, mb: 2 }}
                        disabled={educations.length === 0}
                        onClick={() => {
                            navigate('/upload-candidate-profile-picture')
                        }}
                    >
                        Continue
                    </Button>

                </Box>
            </Container>
        </ThemeProvider>
    );
}