class StorageService {

    static BASIC_URL = "http://KaajServer.ap-south-1.elasticbeanstalk.com"

    static getToken() {
        return localStorage.getItem("kaaj-token");
    }

    static removeToken() {
        localStorage.removeItem('kaaj-token');
        localStorage.removeItem('kaaj-user');
    }

    static tokenExpired() {
        const token = this.getToken();
        if (!token) return true; // Token doesn't exist, consider it expired

        const tokenData = JSON.parse(atob(token.split('.')[1]));
        const expiry = tokenData.exp;
        return Math.floor(Date.now() / 1000) >= expiry;
    }

}



export default StorageService;