import React, { useState, useEffect } from "react";
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { useNavigate } from 'react-router-dom';
import { useParams } from 'react-router-dom';
import MenuItem from '@mui/material/MenuItem';
import InputLabel from '@mui/material/InputLabel';
import Select from '@mui/material/Select';

import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import dayjs from 'dayjs';

import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import AppBar from '@mui/material/AppBar';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import Typography from '@mui/material/Typography';
import StorageService from "../../common/storage";


const defaultTheme = createTheme();

export default function CreateCandidate() {

    const [formData, setFormData] = useState({
        dob: "",
        gender: "",
        maritalStatus: "",
        address: "",
        country: "",
    });
    let { id } = useParams();
    // const [dropDowns, setDropDowns] = useState();
    const [genderOptions, setGenderOptions] = useState([]);
    const [maritalStatus, setMaritalStatus] = useState([]);
    const [country, setCountry] = useState([]);

    const navigate = useNavigate();

    const dobDate = dayjs();

    useEffect(() => {

        const fetchDropDowns = async () => {
            try {
                const response = await fetch(StorageService.BASIC_URL + `/api/auth/enums/all`);
                const data = await response.json();
                console.log(data);
                // setDropDowns(data);
                // const genderData = response.data.gender;
                setGenderOptions(data.gender);
                setMaritalStatus(data.maritalStatus);
                setCountry(data.country);
                setFormData({ ...formData, dob: Date.now() });
                // console.log("genderOptions",genderOptions)
            } catch (error) {
                console.error("Error fetching users:", error.message);
            }
        };

        fetchDropDowns();
    }, [])

    const handleInputChange = (event) => {
        const { name, value } = event.target;
        setFormData({
            ...formData,
            [name]: value,
        });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            if (StorageService.tokenExpired()) {
                navigate("/");
            }
            setOpenBackDrop(true);
            const userData = formData;
            const response = await fetch(StorageService.BASIC_URL + "/api/candidate", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    "Authorization": `Bearer ${localStorage.getItem("kaaj-token")}`
                },
                body: JSON.stringify(userData),
            });
            if (!response.ok) {
                setOpenBackDrop(false);
                throw new Error("Signup failed");
            } else if (response.ok) {
                setOpenBackDrop(false);
                navigate("/view-candidate-languages");
            }

        } catch (error) {
            console.error("Error signup user:", error.message);
        }
    };

    const [openBackDrop, setOpenBackDrop] = React.useState(false);

    const handleCloseBackDrop = () => {
        setOpenBackDrop(false);
    };
    const handleOpenBackDrop = () => {
        setOpenBackDrop(true);
    };

    return (
        <ThemeProvider theme={defaultTheme}>

            <Backdrop
                sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                open={openBackDrop}
                onClick={handleCloseBackDrop}
            >
                <CircularProgress color="inherit" />
            </Backdrop>
            <AppBar position="static">
                <Toolbar variant="dense">
                    <IconButton edge="start" color="inherit" aria-label="menu" sx={{ mr: 2 }}>
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" color="inherit" component="div">
                        Personal Details
                    </Typography>
                </Toolbar>
            </AppBar>

            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <Box
                    sx={{
                        marginTop: 8,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >

                    <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}>
                        <Grid container spacing={2}>


                            <Grid item xs={12}>

                                <LocalizationProvider dateAdapter={AdapterDayjs}>
                                    <DatePicker
                                        label="Date of Birth"
                                        value={dobDate}
                                        required
                                        fullWidth
                                        name="dob"
                                        id="dob"
                                        variant="standard"
                                        onChange={(date) => setFormData({ ...formData, dob: date })}
                                        renderInput={(params) => <TextField {...params} />}
                                        slotProps={{ textField: { variant: "standard", fullWidth: true  } }}
                                    />

                                </LocalizationProvider>

                            </Grid>
                            <Grid item xs={12}>
                                <InputLabel id="gender">Gender</InputLabel>
                                <Select
                                    fullWidth
                                    required
                                    name="gender"
                                    label="Gender *"
                                    id="gender"
                                    value={formData.gender}
                                    onChange={handleInputChange}
                                    variant="standard"
                                >

                                    {Object.entries(genderOptions).map(([key, value]) => (
                                        <MenuItem key={key} value={key}>
                                            {value}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </Grid>
                            <Grid item xs={12}>
                                <InputLabel id="maritalStatus">Marital Status</InputLabel>
                                <Select
                                    fullWidth
                                    required
                                    name="maritalStatus"
                                    label="Marital Status *"
                                    id="maritalStatus"
                                    value={formData.maritalStatus}
                                    onChange={handleInputChange}
                                    variant="standard"
                                >

                                    {Object.entries(maritalStatus).map(([key, value]) => (
                                        <MenuItem key={key} value={key}>
                                            {value}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </Grid>
                            <Grid item xs={12}>
                                <InputLabel id="country">Country</InputLabel>
                                <Select
                                    fullWidth
                                    required
                                    name="country"
                                    label="Country *"
                                    id="country"
                                    value={formData.country}
                                    onChange={handleInputChange}
                                    variant="standard"
                                >

                                    {Object.entries(country).map(([key, value]) => (
                                        <MenuItem key={key} value={key}>
                                            {value}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    required
                                    fullWidth

                                    name="address"
                                    label="Address"
                                    id="address"
                                    autoComplete="address"
                                    value={formData.address}
                                    variant="standard"
                                    onChange={handleInputChange}
                                />
                            </Grid>

                            <Grid item xs={12}>
                                <Button
                                    type="submit"
                                    fullWidth
                                    variant="contained"
                                    sx={{ mt: 3, mb: 2 }}
                                    disabled={!formData.address || !formData.country || !formData.dob || !formData.gender || !formData.maritalStatus}
                                >
                                    Continue
                                </Button>
                            </Grid>
                        </Grid>
                    </Box>
                </Box>
            </Container>
        </ThemeProvider>
    );
}