import React, { useState, useEffect } from "react";
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { useNavigate } from 'react-router-dom';
import { useParams } from 'react-router-dom';
import MenuItem from '@mui/material/MenuItem';
import InputLabel from '@mui/material/InputLabel';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import './css/ViewLanguages.css'

import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
// import { AdapterDayjs } from '@mui/x-date-pickers/adapter-dayjs';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { makeStyles } from '@mui/material/styles';
import Card from '@mui/material/Card';
import { styled } from '@mui/material/styles';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import { initializeApp } from "firebase/app";
import { getStorage, uploadBytes, ref, getDownloadURL } from "firebase/storage";
import FileType from "../../enums/FileType";
import CheckCircleIcon from '@mui/icons-material/CheckCircle';

import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import AppBar from '@mui/material/AppBar';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import StorageService from "../../common/storage";

const VisuallyHiddenInput = styled('input')({
    clip: 'rect(0 0 0 0)',
    clipPath: 'inset(50%)',
    height: 1,
    overflow: 'hidden',
    position: 'absolute',
    bottom: 0,
    left: 0,
    whiteSpace: 'nowrap',
    width: 1,
});

const StyledCard = styled(Card)({
    minWidth: 275,
});

// Initialize Firebase
const firebaseConfig = {
    apiKey: "AIzaSyCfvLfrBB2SHl3UMLVgZBhucrxkZH-s1yE",
    authDomain: "kaaj-2fa4b.firebaseapp.com",
    projectId: "kaaj-2fa4b",
    storageBucket: "kaaj-2fa4b.appspot.com",
    messagingSenderId: "545235033893",
    appId: "1:545235033893:web:df4fc4da3660d8251884cc"
};

const app = initializeApp(firebaseConfig);
const storage = getStorage(app);



const defaultTheme = createTheme();

export default function UploadCandidateDocoments() {

    const [file, setFile] = useState(null);
    const [open, setOpen] = React.useState(false);
    const [documentUploadVerification, setDocumentUploadVerification] = useState(false);

    const handleClose = () => {
        setOpen(false);
    };
    const handleOpen = () => {
        setOpen(true);
    };

    const handleFileChange = (e) => {
        setFile(e.target.files[0]);
        console.log(e.target.files[0]);
        console.log("file", file);

        uploadFileInKaaj();
    };

    const globalHandleFileChange = (e, fileType) => {
        setFile(e.target.files[0]);
        console.log(e.target.files[0]);
        console.log("file", file);

        uploadFileInKaaj(e.target.files[0], fileType);
    };

    // const handleUpload = () => {
    //     if (file) {
    //         const storageRef = ref(storage, `uploads/${file.name}`);
    //         await uploadBytes(storageRef, file);
    //     }
    //   };
    const uploadFileInKaaj = async (file, fileType) => {
        if (StorageService.tokenExpired()) {
            navigate("/");
        }
        setOpen(true);
        const candidateFile = await uploadFileOnFireBase(file, fileType);
        console.log(candidateFile);
        if (candidateFile != null) {
            console.log("upload done");
            const response = await fetch("http://localhost:8080/api/candidate/file", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    "Authorization": `Bearer ${localStorage.getItem("kaaj-token")}`
                },
                body: JSON.stringify(candidateFile),
            });
            if (!response.ok) {
                throw new Error("Signup failed");
                setOpen(false);
            } else if (response.ok) {
                // navigate("/add-skills");
                fetchDropDowns();
                setOpen(false);
            }
        }

    }

    const generateFileName = () => {
        const currentDate = new Date();
        const year = currentDate.getFullYear();
        const month = String(currentDate.getMonth() + 1).padStart(2, '0');
        const day = String(currentDate.getDate()).padStart(2, '0');
        const hours = String(currentDate.getHours()).padStart(2, '0');
        const minutes = String(currentDate.getMinutes()).padStart(2, '0');
        const seconds = String(currentDate.getSeconds()).padStart(2, '0');

        const fileName = `file_${year}${month}${day}_${hours}${minutes}${seconds}`;
        console.log("filename", fileName);
        return fileName;
    }

    const uploadFileOnFireBase = async (file, fileType) => {
        if (!file) return;
        const fileName = file.name;
        const fileExtension = fileName.slice(((fileName.lastIndexOf(".") - 1) >>> 0) + 2);
        const storageRef = ref(storage, `docoments/${generateFileName()}.${fileExtension}`);
        const res = await uploadBytes(storageRef, file);
        console.log("upload done", res)
        console.log("downloadTokens", res.metadata.downloadTokens);
        const downloadURL = await getDownloadURL(storageRef);
        const token = extractTokenFromUrl(downloadURL);
        console.log(downloadURL);

        const candidateFile = {
            fileName: res.metadata.fullPath,
            token: token,
            fullPath: downloadURL,
            fileType: fileType
        }

        return candidateFile;
    };


    const navigate = useNavigate();


    const fetchDropDowns = async () => {
        try {
            setOpen(true);
            const response = await fetch(StorageService.BASIC_URL + `/api/candidate/file/verification`, {
                method: "GET",
                headers: {
                    "Content-Type": "application/json",
                    "Authorization": `Bearer ${localStorage.getItem("kaaj-token")}`
                },
            });
            const data = await response.json();
            console.log(data);
            setDocumentUploadVerification(data);
            setOpen(false);
        } catch (error) {
            console.error("Error fetching users:", error.message);
        }
    };

    useEffect(() => {


        fetchDropDowns();
    }, [])

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            if (StorageService.tokenExpired()) {
                navigate("/");
            }
            const response = await fetch(StorageService.BASIC_URL + "/api/candidate/info-added", {
                method: "GET",
                headers: {
                    "Content-Type": "application/json",
                    "Authorization": `Bearer ${localStorage.getItem("kaaj-token")}`
                }
            });
            if (!response.ok) {
                throw new Error("Signup failed");
            } else if (response.ok) {
                navigate("/dashboard");
            }
        } catch (error) {
            console.error("Error signup user:", error.message);
        }
    };

    const extractTokenFromUrl = (fullUrl) => {
        const url = new URL(fullUrl);
        const token = url.searchParams.get("token");
        console.log(token);
        return token;
    };




    return (
        <ThemeProvider theme={defaultTheme}>
            <Backdrop
                sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                open={open}
                onClick={handleClose}
            >
                <CircularProgress color="inherit" />
            </Backdrop>
            <AppBar position="static">
                <Toolbar variant="dense">
                    <IconButton edge="start" color="inherit" aria-label="menu" sx={{ mr: 2 }}>
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" color="inherit" component="div">
                        Upload documents
                    </Typography>
                </Toolbar>
            </AppBar>
            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <Box
                    sx={{
                        marginTop: 8,
                        // display: 'flex',
                        // flexDirection: 'column',
                        // alignItems: 'center',
                    }}
                >
                    <Typography sx={{ pt: 2, pb: 2 }}>Passport Copy</Typography>
                    {!documentUploadVerification.passportCopy && (<Button
                        component="label"
                        role={undefined}
                        variant="contained"
                        tabIndex={-1}
                        startIcon={<CloudUploadIcon />}
                        fullWidth
                    >
                        Upload file
                        <VisuallyHiddenInput type="file" onChange={(e) => { globalHandleFileChange(e, FileType.PASSPORT); }} />
                    </Button>
                    )}


                    {documentUploadVerification.passportCopy && (<Button
                        component="label"
                        role={undefined}
                        variant="contained"
                        tabIndex={-1}
                        startIcon={<CheckCircleIcon />}
                        fullWidth
                        color="success"
                    >
                        Passport Copy Uploaded
                    </Button>
                    )}

                    <Typography sx={{ pt: 2, pb: 2 }}>Residence Visa (optional)</Typography>
                    {!documentUploadVerification.residenceVisa && (<Button
                        component="label"
                        role={undefined}
                        variant="contained"
                        tabIndex={-1}
                        startIcon={<CloudUploadIcon />}
                        fullWidth
                    >
                        Upload file
                        <VisuallyHiddenInput type="file" onChange={(e) => { globalHandleFileChange(e, FileType.RESIDENCE_VISA); }} />
                    </Button>
                    )}

                    {documentUploadVerification.residenceVisa && (<Button
                        component="label"
                        role={undefined}
                        variant="contained"
                        tabIndex={-1}
                        startIcon={<CheckCircleIcon />}
                        fullWidth
                        color="success"
                    >
                        Residence Visa Uploaded
                    </Button>
                    )}

                    <Typography sx={{ pt: 2, pb: 2 }}>National ID Card</Typography>
                    {!documentUploadVerification.nationalIDCard && (<Button
                        component="label"
                        role={undefined}
                        variant="contained"
                        tabIndex={-1}
                        startIcon={<CloudUploadIcon />}
                        fullWidth
                    >
                        Upload file
                        <VisuallyHiddenInput type="file" onChange={(e) => { globalHandleFileChange(e, FileType.NATIONAL_ID); }} />
                    </Button>
                    )}

                    {documentUploadVerification.nationalIDCard && (<Button
                        component="label"
                        role={undefined}
                        variant="contained"
                        tabIndex={-1}
                        startIcon={<CheckCircleIcon />}
                        fullWidth
                        color="success"
                    >
                        National ID Card Uploaded
                    </Button>
                    )}

                    <Typography sx={{ pt: 2, pb: 2 }}> Your Resume/CV (optional)</Typography>
                    {!documentUploadVerification.resume && (<Button
                        component="label"
                        role={undefined}
                        variant="contained"
                        tabIndex={-1}
                        startIcon={<CloudUploadIcon />}
                        fullWidth
                    >
                        Upload file
                        <VisuallyHiddenInput type="file" onChange={(e) => { globalHandleFileChange(e, FileType.RESUME); }} />
                    </Button>
                    )}

                    {documentUploadVerification.resume && (<Button
                        component="label"
                        role={undefined}
                        variant="contained"
                        tabIndex={-1}
                        startIcon={<CheckCircleIcon />}
                        fullWidth
                        color="success"
                    >
                        Your Resume/CV Uploaded
                    </Button>
                    )}

                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        sx={{ mt: 3, mb: 2 }}
                        disabled={!documentUploadVerification.passportCopy || !documentUploadVerification.nationalIDCard}
                        onClick={handleSubmit}
                    >
                        Submit
                    </Button>

                </Box>
            </Container>
        </ThemeProvider>
    );
}