import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { useNavigate } from 'react-router-dom';
import { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';

import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import AppBar from '@mui/material/AppBar';
import Snackbar from '@mui/material/Snackbar';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import StorageService from '../../../common/storage';

const defaultTheme = createTheme();

export default function SignUp() {

    const [formData, setFormData] = useState({
        phone: "",
        password: "",
        phoneCode: ""
    });
    let { id } = useParams();
    const [phoneCodes, setPhoneCodes] = useState([]);

    const navigate = useNavigate();

    const handleInputChange = (event) => {
        const { name, value } = event.target;
        console.log(event.target);
        setFormData({
            ...formData,
            [name]: value,
        });
    };

    useEffect(() => {
        const fetchDropDowns = async () => {
            try {
                const response = await fetch(StorageService.BASIC_URL + `/api/auth/enums/all`);
                const data = await response.json();
                console.log(data);
                setPhoneCodes(data.phoneCode);
            } catch (error) {
                console.error("Error fetching users:", error.message);
            }
        };

        fetchDropDowns();
    }, [])

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const userData = formData;
            userData.name = id.id;
            userData.phone = userData.phoneCode + userData.phone
            const response = await fetch("http://localhost:8080/api/auth/signup", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify(userData),
            });
            if (!response.ok) {
                setOpen(true);
            } else if (response.ok) {
                try {
                    const response = await fetch("http://localhost:8080/api/auth/login", {
                        method: "POST",
                        headers: {
                            "Content-Type": "application/json",
                        },
                        body: JSON.stringify(formData),
                    });

                    if (!response.ok) {
                        throw new Error("Invalid credentials");
                    }
                    const data = await response.json();
                    console.log(data);
                    if (data.jwt) {
                        localStorage.setItem("kaaj-token", data.jwt);
                        const user = {
                            id: data.userId,
                            role: data.userRole
                        }
                        localStorage.setItem("kaaj-user", JSON.stringify(user));
                        navigate("/create-candidate");
                    }
                } catch (error) {
                    console.error("Error authenticating user:", error.message);
                }
            }
        } catch (error) {
            console.error("Error signup user:", error.message);
        }
    };

    //Snakebar

    const [open, setOpen] = React.useState(false);

    const handleClick = () => {
        setOpen(true);
    };

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }

        setOpen(false);
    };

    return (
        <ThemeProvider theme={defaultTheme}>
            <AppBar position="static">
                <Toolbar variant="dense">
                    <IconButton edge="start" color="inherit" aria-label="menu" sx={{ mr: 2 }}>
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" color="inherit" component="div">
                        Kaaj
                    </Typography>
                </Toolbar>
            </AppBar>

            <Snackbar open={open} autoHideDuration={6000} anchorOrigin={{ vertical: 'top', horizontal: 'center' }} onClose={handleClose} message=" User Already Exists With Same Email.">

            </Snackbar>

            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <Box
                    sx={{
                        marginTop: 8,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >
                    <Avatar sx={{ m: 1, bgcolor: 'primary.main' }}>
                        <LockOutlinedIcon />
                    </Avatar>
                    <Typography component="h1" variant="h5">
                        Sign up
                    </Typography>
                    <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}>
                        <Grid container spacing={2}>
                            {/* <Grid item xs={12}> */}

                            {/* </Grid> */}
                            <Grid item xs={12} style={{ display: 'flex', alignItems: 'center' }}>
                                <FormControl variant="standard" style={{ width: '30%', marginRight: '2%' }}>
                                    <InputLabel id="phoneCode">Code</InputLabel>
                                    <Select
                                        fullWidth
                                        required
                                        labelId="phoneCode"
                                        name="phoneCode"
                                        id="phoneCode"
                                        value={formData.phoneCode}
                                        onChange={handleInputChange}

                                    >
                                        {Object.entries(phoneCodes).map(([key, value]) => (
                                            <MenuItem key={key} value={value}>
                                                {value}
                                            </MenuItem>
                                        ))}
                                    </Select>
                                </FormControl>
                                <TextField
                                    required
                                    fullWidth
                                    id="phone"
                                    label="Phone Number"
                                    name="phone"
                                    autoComplete="phone"
                                    value={formData.phone}
                                    variant="standard"
                                    onChange={handleInputChange}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    required
                                    fullWidth
                                    name="password"
                                    label="Password"
                                    type="password"
                                    id="password"
                                    autoComplete="new-password"
                                    value={formData.password}
                                    variant="standard"
                                    onChange={handleInputChange}
                                />
                            </Grid>
                        </Grid>
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            sx={{ mt: 3, mb: 2 }}
                            disabled={!formData.phone || !formData.password}
                        >
                            Sign Up
                        </Button>

                        <Button
                            fullWidth
                            variant="outlined"
                            onClick={() => {
                                navigate('/login')
                            }}
                        >
                            Already have an account?
                        </Button>

                    </Box>
                </Box>
            </Container>
        </ThemeProvider>
    );
}